import { getTopicSublist } from '@/request/api/collect';
export default {
  data() {
    return {
      // 收藏的专栏数
      topicNum: 0
    };
  },

  async mounted() {
    if (sessionStorage.getItem("cookie")) {
      // 获取已收藏专栏
      const res = await getTopicSublist(sessionStorage.getItem("cookie")); // console.log('获取已收藏专栏', res);

      this.topicNum = res.data.count;
    }
  }

};